@import "@Styles/_variables.scss";
@import "@Styles/_mixins.scss";

.reviews {
  padding-right: 0.5rem;
  height: 300px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 2rem;

  &__block {
    margin-bottom: 0.75rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }

  &__image {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: contain;
  }

  &__author {
    font-family: $font-secondary;
    @include font(500, 0.95rem, 1.375rem);
    text-decoration: underline;
  }

  &__text {
    font-size: 0.875rem;
    line-height: 1.185rem;
    font-style: italic;
  }

  &__message {
    text-align: center;
    text-decoration: underline;
  }
}

@media (max-width: 991.98px) {
  .reviews {
    height: auto;
    max-height: 300px;
  }
}
